/**
 * queryString.parse
 * @param  { string } string
 * @returns { Object }
 * @example
 *
 * parse('id=1&id=2&name=jsl')
 * // => { id: ['1', '2'], name: 'jsl' }
 *
 * parse('id=1&code=p')
 * // => { id: '1', code: 'p' }
 */
export function parse(string: string) {
  const object: Record<string, string | string[]> = {}
  if (string.length === 0) return {}
  for (const keyValue of string.split('&')) {
    const [key, value] = keyValue.split('=')
    object[key] = Object.prototype.hasOwnProperty.call(object, key)
      ? ([] as string[]).concat(object[key], decodeURIComponent(value))
      : decodeURIComponent(value)
  }
  return object
}

/**
 * queryString.stringify
 * @param  { object } object
 * @return { string }
 * @example
 *
 * stringify({ id: '1', code: 'p' })
 * // => 'id=1&code=p'
 *
 * stringify({ id: ['1', '2'], name: 'jsl' })\
 * // => 'id=1&id=2&name=jsl'
 */
export function stringify(object: Record<string, any>) {
  const result = []
  for (const key in object) {
    const value = object[key]
    const query = Array.isArray(value)
      ? value.reduce((a, b) => `${key}=${encodeURIComponent(a)}` + `&${key}=${encodeURIComponent(b)}`)
      : `${key}=${encodeURIComponent(value)}`
    result.push(query)
  }
  return result.join('&')
}

export default { parse, stringify }
