import { parse } from '../utils/queryString'
import appendSearch from '../utils/appendSearch'
import { JSBridge, SchemeParams } from './JSBridge'
import { RealseeVRWebviewData, RealseeVRWebviewInstance, RealseeVRWebviewProps } from './typings'
import { getRsVrWebviewProps } from './utils'
import { Subscribe } from '../utils/subscribe'
import TRTC from 'trtc-wx-sdk'

interface WebviewState {
  active: boolean
  orientation: 'Portrait' | 'Landscape'
  minimized: number
}

type ControllerEventMap = {
  webviewStateChange(webviewState: WebviewState): void
  error(err: Error): void
  weakNetwork(isWeakNetwork: boolean): void
  userVolumes(userVolumes: { userId: string; audioVolume: number }[]): void
}

const defaultRtcConfig = {
  /**
   * 是否开麦
   */
  enableMic: true,
  /**
   * 是否开启摄像头
   */
  enableCamera: false,
  /**
   * 是否开启增益
   */
  enableAgc: true,
  /**
   * 是否开启降噪
   */
  enableAns: true,
  /**
   * 是否开启远程镜像
   */
  enableRemoteMirror: false,
  /**
   * 是否开启手势缩放
   */
  enableZoom: false,
}

export class Controller extends Subscribe<ControllerEventMap> {
  private _props!: RealseeVRWebviewProps
  private _jsBridge: JSBridge
  private _component: RealseeVRWebviewInstance

  private _webviewState: WebviewState = {
    active: false,
    orientation: 'Portrait',
    minimized: 0,
  }

  constructor(component: RealseeVRWebviewInstance) {
    super()
    this._component = component
    this._jsBridge = new JSBridge({
      onReady: this._onReady,
      onClose: this._onClose,
      onMessage: this._onMessage,
      onError: this._onError,
      onRequest: this._onRequest,
      onScheme: this._onScheme,
    })
    this._addInnerEventLister()
  }

  init = async (props: string | RealseeVRWebviewProps) => {
    this._props = typeof props === 'string' ? getRsVrWebviewProps(props) : props
    try {
      this._checkProps(this._props)
      if (!this._props.options.rtc?.trtc) {
        this._props.options.rtc = Object.assign(this._props.options.rtc || {}, { trtc: new TRTC(this._component) })
      }
      await this._jsBridge.init(this._props!)
      const wssQuery4Webview = this._jsBridge.wssQuery4Webview || ''
      let url = appendSearch(this._props!.url, wssQuery4Webview)

      // 加open_app_id
      try {
        const open_app_id = await this._props.options.app?.key
        if (open_app_id) {
          url = appendSearch(url, `open_app_id=${open_app_id}`)
        }
      } catch (e) {}

      // 加wx_token
      try {
        const token = await this._props.behaviors?.getToken?.()
        if (token) {
          url = appendSearch(url, `wx_token=${token}`)
        }
      } catch (e) {}

      // 加埋点的rstpsid
      try {
        const rstpsid = await wx.getStorageSync('__Realsee_Telemetry_Session_Storage__')
        if (typeof rstpsid === 'string' && rstpsid !== '') {
          url = appendSearch(url, `rstpsid=${rstpsid}`)
        }
      } catch (e) {}
      await this._setDataAsync({ url })
    } catch (e) {
      this._onError(e)
    }
  }

  dispose = () => {
    try {
      this._rtcQuit()
    } catch (e) {}
    this._jsBridge.dispose()
    this._disposers.forEach((disposer) => disposer())
    this._disposers = []
  }

  private _disposers: (() => void)[] = []

  private _addInnerEventLister = () => {
    // 监听webview变化
    const _onAppShow = () => {
      this._webviewState.active = true
      this.emit('webviewStateChange', this._webviewState)
    }
    const _onAppHide = () => {
      this._webviewState.active = false
      this.emit('webviewStateChange', this._webviewState)
    }
    wx.onAppShow(_onAppShow)
    wx.onAppHide(_onAppHide)
    this._disposers.push(
      () => wx.offAppShow(_onAppShow),
      () => wx.offAppHide(_onAppHide),
    )
  }

  /**
   * 检查props的合法性
   * @param props
   */
  private _checkProps = (props: RealseeVRWebviewProps | undefined) => {
    // 1、检查props
    if (!props) throw new Error('unavailable props, please check!')

    let { url, options } = props
    // 2、检查url
    if (!url) throw new Error('unavailable url, please check!')

    // 3、检查options
    if (!options) throw new Error('unavailable options, please check!')

    const { app } = options
    if (!app?.key || !app?.secret) throw new Error('unavailable app options, please check!')
  }

  private _setData = (data: Partial<RealseeVRWebviewData>, callback?: () => void) => {
    this._component.setData(data, callback)
  }

  private _setDataAsync = async (data: Partial<RealseeVRWebviewData>) => {
    return new Promise<void>((success) => this._setData(data, () => success()))
  }

  get data() {
    return this._component.data
  }

  /**
   * 全局报错透传
   * @param error
   */
  private _onError = (error: Error) => {
    if (typeof this._props.behaviors?.onError === 'function') {
      this._props.behaviors?.onError(error)
    } else {
      throw error
    }
  }

  private _onMessage = (message: string) => {
    console.log(message)
  }

  private _onReady = () => {}

  private _onClose = () => {}

  private _onRequest = async (method: string, args: any): Promise<any> => {
    // 先走 behaviors
    const { behaviors = {} } = this._props
    if (typeof behaviors[method] === 'function') {
      return await behaviors[method](args)
    }

    // 再走wx的方法
    if (wx.canIUse(method)) {
      return new Promise((success, fail) =>
        (wx as any)[method]({ ...args, success, fail: (err: any) => fail(err.errMsg) }),
      )
    }

    // 最后报错
    return Promise.reject(`behavior=${method} not implemented`)
  }

  private _innerSchemes: Record<string, InnerSchemeCallback> = {
    'common/staticData': async (opts: InnerSchemeCallbackOptions) =>
      await this._innerSchemeCall(opts, this._getStaticData),
    'common/loadProgress': ({ callback }: InnerSchemeCallbackOptions) => callback(true),
    'common/closeLoading': ({ callback }: InnerSchemeCallbackOptions) => callback(true),
    'common/minimize': ({ callback }: InnerSchemeCallbackOptions) => callback(new Error('小程序不支持该方法')),
    'common/setOrientation': ({ callback }: InnerSchemeCallbackOptions) => callback(new Error('小程序不支持该方法')),
    'common/keepScreenLight': ({ callback, params }: InnerSchemeCallbackOptions) =>
      wx.setKeepScreenOn({
        keepScreenOn: Number(params.enable) === 1,
        success: ({ errMsg }) => callback(errMsg),
        fail: ({ errMsg }) => callback(new Error(errMsg)),
      }),
    'common/closeWebView': async (opts: InnerSchemeCallbackOptions) =>
      await this._innerSchemeCall(opts, this._props.behaviors?._onExitVr),
    'common/saveImage2Album': ({ callback, params }: InnerSchemeCallbackOptions) =>
      wx.saveImageToPhotosAlbum({
        filePath: params.base64,
        success: ({ errMsg }) => callback(errMsg),
        fail: ({ errMsg }) => callback(new Error(errMsg)),
      }),
    'common/preload': ({ callback, params }: InnerSchemeCallbackOptions) => {
      const urls: string[] = JSON.parse(params.urls)
      const promises = urls.map(
        (url) =>
          new Promise((resolve) =>
            wx.request({ url, success: ({ errMsg }) => resolve(errMsg), fail: ({ errMsg }) => resolve(errMsg) }),
          ),
      )
      Promise.all(promises).then((res) => callback(res))
    },
    'common/shock': ({ callback }: InnerSchemeCallbackOptions) =>
      wx.vibrateLong({
        success: ({ errMsg }) => callback(errMsg),
        fail: ({ errMsg }) => callback(new Error(errMsg)),
      }),
    'common/getBangsHeight': ({ callback }: InnerSchemeCallbackOptions) => callback(0),
    'common/getDeviceInfo': async (opts: InnerSchemeCallbackOptions) =>
      await this._innerSchemeCall(opts, this._getStaticData),
    'common/getUserInfo': async (opts: InnerSchemeCallbackOptions) =>
      await this._innerSchemeCall(opts, this._props?.behaviors?.getUserInfo),
    'common/login': async (opts: InnerSchemeCallbackOptions) =>
      await this._innerSchemeCall(opts, this._props?.behaviors?.requestLogin),
    'common/getWebViewState': ({ callback }: InnerSchemeCallbackOptions) => callback(this._webviewState),
    'common/detectMicro': async (opts: InnerSchemeCallbackOptions) =>
      await this._innerSchemeCall(opts, this._rtcDetectMicro),
    'common/webViewState': ({ callback, type }: InnerSchemeCallbackOptions) => {
      if (type === 'callAndBackfeed') {
        callback(this._webviewState)
      } else {
        const handler = (webviewState: WebviewState) => callback(webviewState)
        this.on('webviewStateChange', handler)
        this._disposers.push(() => this.off('webviewStateChange', handler))
      }
    },
    'common/webViewError': ({ callback }: InnerSchemeCallbackOptions) => {
      const handler = (err: Error) => callback(err.message)
      this.on('error', handler)
      this._disposers.push(() => this.off('error', handler))
    },
    'common/actionShare': async (opts: InnerSchemeCallbackOptions) =>
      await this._innerSchemeCall(opts, this._props.behaviors?.actionShare),
    'live/join': async (opts: InnerSchemeCallbackOptions) => await this._innerSchemeCall(opts, this._rtcJoin),
    'live/toggleMicro': async (opts: InnerSchemeCallbackOptions) =>
      await this._innerSchemeCall(opts, this._rtcToggleMicro),
    'live/quit': async (opts: InnerSchemeCallbackOptions) => await this._innerSchemeCall(opts, this._rtcQuit),
    'live/weakNetwork': ({ callback }: InnerSchemeCallbackOptions) => {
      const handler = (isWeakNetwork: boolean) => callback(isWeakNetwork)
      this.on('weakNetwork', handler)
      this._disposers.push(() => this.off('weakNetwork', handler))
    },
    'live/userVolumes': ({ callback }: InnerSchemeCallbackOptions) => {
      const handler = (userVolumes: { userId: string; audioVolume: number }[]) => callback(userVolumes)
      this.on('userVolumes', handler)
      this._disposers.push(() => this.off('userVolumes', handler))
    },
  }

  private _onScheme = ({ actionUrl, type = 'callAndBackfeed', callback }: SchemeParams) => {
    const baseScheme = this._props.options?.info?.scheme || ''
    const url = actionUrl.replace(baseScheme + '://', '')
    const [scheme, query = ''] = url.split('?')
    const params = parse(query)

    const { schemes = {} } = this._props

    // 用户定义的 scheme
    if (typeof schemes[scheme] === 'function') {
      return schemes[scheme](callback, type, params)
    }

    // 内置的schemes
    if (typeof this._innerSchemes[scheme] === 'function') {
      return this._innerSchemes[scheme].call(this, { callback, type, params })
    }

    return callback(new Error(`scheme=(${scheme}) not implemented`))
  }

  /**
   * webview发生了错误
   * @param err
   */
  onWebViewError = (err: Error) => {
    this._props.behaviors?.onWebViewError?.(err)
    this.emit('error', err)
  }

  private _getStaticData = async () => {
    const staticData: any = {}
    let info = this._props.options.info || {}
    try {
      const systemInfo = await wx.getSystemInfo()
      staticData.sysModel = systemInfo.model
      staticData.sysVersion = systemInfo.system
    } catch (e) {
      this._onError(e)
    }
    try {
      const { networkType } = await wx.getNetworkType()
      staticData.network = networkType
    } catch (e) {
      this._onError(e)
    }
    return { ...info, ...staticData }
  }

  private _innerSchemeCall = async (
    { callback, type, params }: InnerSchemeCallbackOptions,
    schemeMethod?: (params: any) => Promise<any>,
  ) => {
    if (type === 'callAndBackfeed') {
      try {
        const result = await schemeMethod?.(params)
        callback(result)
      } catch (e) {
        callback(e)
      }
    } else {
      callback(new Error(`unavailable scheme type=(${type})`))
    }
  }

  /**
   * 以下是小程序trtc相关的方法
   */

  /**
   * 检查麦克风权限
   * @returns
   */
  _rtcDetectMicro = async () => {
    let result = false
    const setting = await wx.getSetting()
    if (setting.authSetting['scope.record']) return true

    result = await new Promise((res) =>
      wx.authorize({ scope: 'scope.record', success: () => res(true), fail: () => res(false) }),
    )
    if (result) return result

    result = await new Promise((res) =>
      wx.showModal({
        content: '您尚未授权麦克风',
        confirmText: '去设置',
        success: (resp) => {
          if (resp.cancel) {
            res(false)
          }
          if (resp.confirm) {
            wx.openSetting({
              success: (re) => res(re.authSetting['scope.record'] || false),
              fail: () => res(false),
            })
          }
        },
        fail: () => res(false),
      }),
    )

    return result
  }

  /**
   * 进入带看
   * @param params
   * @returns
   */
  _rtcJoin = async (params: any) => {
    return new Promise<boolean>(async (resolve, reject) => {
      try {
        if (params.sdkAppID && params.userID && params.userSig && params.roomID) {
          await this._jsBridge.rtc.initializeV2(params, defaultRtcConfig)
        } else if (params.config) {
          await this._jsBridge.rtc.oldInitialize({
            ...params,
            rtcConfig: defaultRtcConfig,
          })
        } else if (params.roomID && params.userID) {
          await this._jsBridge.rtc.initialize({ ...params, rtcConfig: defaultRtcConfig })
        }
        const pusher = await this._jsBridge.rtc.enterRoom()
        await this._setDataAsync({
          liveMode: true,
          pusher,
        })
        this._jsBridge.rtc.start()
        resolve(true)
      } catch (e) {
        this._onError(e)
        reject(e)
      }
    })
  }

  /**
   * 退出带看
   * @returns
   */
  _rtcQuit = async () => {
    return new Promise<boolean>(async (resolve, reject) => {
      try {
        await this._jsBridge.rtc.stop()
        const { pusher = {}, playerList = [] } = await this._jsBridge.rtc.exitRoom()
        await this._setDataAsync({
          liveMode: false,
          pusher,
          playerList,
        })
        resolve(true)
      } catch (e) {
        this._onError(e)
        reject(e)
      }
    })
  }

  /**
   * 控制麦克风
   * @param params
   * @returns
   */
  _rtcToggleMicro = async (params: { flag: 1 | 0 | true }) => {
    return new Promise<boolean>(async (resolve, reject) => {
      const state = Number(params.flag) === 1 || params.flag === true
      try {
        const pusher = await this._jsBridge.rtc.toggleMicrophone(state)
        await this._setDataAsync({ pusher })
        resolve(true)
      } catch (e) {
        this._onError(e)
        reject(e)
      }
    })
  }
}

type InnerSchemeCallback = (opts: InnerSchemeCallbackOptions) => void
type InnerSchemeCallbackOptions = {
  callback: (result: any) => void
  type: 'callAndBackfeed' | 'callAndListen'
  params: any
}
