import MD5 from 'crypto-js/md5'
interface IGenerateSocketParamsOpts {
  ak: string
  sk: string
  origin: string
  members: {
    weapp: string
    web: string
  }
  endpoint: string
  tpl: string
  qs: string
}
const generateUUID = () =>
  'xxxxxxxxxxxx4xxxyxxxxxxxxxxxxxxx'.replace(/[xy]/g, (c) => {
    const r = (Math.random() * 16) | 0
    const v = c === 'x' ? r : (r & 0x3) | 0x8
    return v.toString(16)
  })

interface IgenerateSignatureParams {
  ts: string
  ak: string
  sk: string
  uuid: string
  member: string
}

const generateSignature = ({ ts, ak, sk, uuid, member }: IgenerateSignatureParams) =>
  MD5(`${ts}_${ak}_${sk}_${uuid}_${member}`).toString().toUpperCase()

type TGenerateWssUrlParams = Record<string, string> & {
  origin: string
  endpoint: string
  uuid: string
  member: string
  ak: string
  ts: string
  sign: string
}

export const generateWssUrl = (tpl: string, params: TGenerateWssUrlParams) => {
  const url = Object.keys(params).reduce((res, curr: string) => res.replace(`{{${curr}}}`, `${params[curr]}`), tpl)
  return encodeURI(url)
}

type TGenerateWssQueryParams = Record<string, string> & {
  uuid: string
  ts: string
  sign: string
}

export const generateWssQuery = (tpl: string, params: TGenerateWssQueryParams) => {
  const query = Object.keys(params).reduce((res, curr: string) => res.replace(`{{${curr}}}`, `${params[curr]}`), tpl)
  return query
}

export default function generateSocketParams(opts: IGenerateSocketParamsOpts) {
  const { ak, sk, origin, members, endpoint, tpl, qs } = opts
  const ts = `${Date.now()}`
  const uuid = generateUUID()

  const signature4Weapp = generateSignature({ ts, ak, sk, uuid, member: members.weapp })
  const wssUrl4Weapp = generateWssUrl(tpl, {
    origin,
    endpoint,
    uuid,
    member: members.weapp,
    ak,
    ts,
    sign: signature4Weapp,
  })
  const signature4Webview = generateSignature({ ts, ak, sk, uuid, member: members.web })
  const wssQuery4Webview = generateWssQuery(qs, { uuid, ts, sign: signature4Webview })
  // const getWssUrl4WebView = ({ wssid, wstsp, wssig }: { wskey: string, wssid: string; wstsp: string; wssig: string }) =>
  //   generateWssUrl(tpl, { origin, endpoint, uuid: wssid, member: members.web, ak, ts: wstsp, sign: wssig })

  return {
    wssUrl4Weapp,
    wssQuery4Webview,
    // getWssUrl4WebView,
  }
}
