import { Subscribe } from '../utils/subscribe'
import TRTC, { PlayerAttributes, PusherAttributes } from 'trtc-wx-sdk'
import generateAuthorization from '../utils/authorization'
import { RealseeVRWebviewAppOptions } from './typings'

export interface TRTCOptions {
  sdkAppID: number
  userID: string
  userSig: string
  roomID: number
}

export interface RtcInitOptions {
  roomID: number
  userID: string
  rtcConfig?: PusherAttributes
}

export interface RtcOptions {
  attendence: {
    attendee_id: string | null
    external_user_id: string
    join_token: string
  }
  config: { app_id: string; bypass: string | null; passport: string; vendor: string }
  expire_time: number
  identifier: string
  user_id: string
  user_sig: string
  vendor_wrapper: string
  voiceId: string
}

export class RTC extends Subscribe<{ [eventName: string]: (ev: any) => void }> {
  private _trtc: TRTC
  private _app: RealseeVRWebviewAppOptions
  private _trtcOptions!: TRTCOptions
  ready: boolean = false

  constructor(options: RealseeVRWebviewAppOptions, rtcConfig: { trtc: TRTC }) {
    super()
    this._app = options
    this._trtc = rtcConfig.trtc
    this._registerRtcEventHandler()
  }

  /**
   * 进行rtc的初始化
   * @param opts
   * @returns
   */
  async initialize(opts: RtcInitOptions): Promise<PusherAttributes> {
    const { roomID, userID, rtcConfig = {} } = opts
    const data = {
      voice_id: roomID,
      user_id: userID,
    }
    const auth = generateAuthorization({
      timestamp: Number((Date.now() / 1000).toFixed(0)),
      appKey: this._app.key,
      appSecret: this._app.secret,
      uri: this._app.sign!,
      method: 'GET',
      data,
    })
    const options: RtcOptions = await this._initOptions(data, auth)
    this._trtcOptions = {
      sdkAppID: Number(options.config.app_id),
      userID: options.user_id,
      userSig: options.user_sig,
      roomID,
    }
    return this._trtc!.createPusher(rtcConfig).pusherAttributes || {}
  }

  /**
   * 兼容老的逻辑
   * @deprecated
   * @param options
   * @returns
   */
  oldInitialize = async (options: RtcOptions, rtcConfig: PusherAttributes = {}) => {
    this._trtcOptions = {
      sdkAppID: Number(options.config.app_id),
      userID: options.user_id,
      userSig: options.user_sig,
      roomID: Number(options.voiceId),
    }
    return this._trtc!.createPusher(rtcConfig).pusherAttributes
  }

  /**
   *
   * @param options
   * @param rtcConfig
   * @returns
   */
  initializeV2 = async (options: TRTCOptions, rtcConfig: PusherAttributes = {}) => {
    this._trtcOptions = {
      sdkAppID: Number(options.sdkAppID),
      userID: options.userID,
      userSig: options.userSig,
      roomID: options.roomID,
    }
    return this._trtc!.createPusher(rtcConfig).pusherAttributes
  }

  /**
   * 去网关取签名配置
   * @param data
   * @param auth
   * @returns
   */
  async _initOptions(data: object, auth: string) {
    return new Promise<RtcOptions>((resolve, reject) => {
      if (!this._app.sign) throw new Error('app配置项`sign`不得为空')
      wx.request({
        url: `${this._app.gateway}${this._app.sign}`,
        data,
        header: {
          'x-request-appid': this._app.key,
          Authorization: auth,
        },
        success: async (res) => {
          if (!res.data) {
            return reject('')
          }
          try {
            const resData = res.data as WechatMiniprogram.IAnyObject
            const rtcOptions = resData.data as RtcOptions
            resolve(rtcOptions)
          } catch (e) {
            reject(e)
          }
        },
        fail: (err) => reject(err),
      })
    })
  }

  /**
   * 开始推流
   */
  start(): void {
    this._trtc!.getPusherInstance()?.start?.()
  }

  /**
   * 结束推流
   */
  stop(): void {
    this._trtc!.getPusherInstance()?.stop?.()
  }

  /**
   * 进入房间
   * @returns
   */
  async enterRoom(): Promise<PusherAttributes> {
    return this._trtc!.enterRoom(this._trtcOptions)
  }

  /**
   * 退出房间
   * @returns
   */
  async exitRoom(): Promise<{ pusher: PusherAttributes; playerList: PlayerAttributes[] }> {
    if (!this._trtc!.getPusherInstance()) return { pusher: {}, playerList: [] }
    return this._trtc!.exitRoom()
  }

  /**
   * 切换麦克风
   * @param state 麦克风状态
   * @returns
   */
  async toggleMicrophone(state: boolean): Promise<PusherAttributes | undefined> {
    return this._trtc?.setPusherAttributes({ enableMic: state })
  }

  /**
   * 获取播放列表
   * @returns
   */
  async getPlayerList(): Promise<PlayerAttributes[]> {
    return await this._trtc!.getPlayerList()
  }
  /**
   * 注册rtc事件
   */
  _registerRtcEventHandler() {
    Object.keys(this._trtc!.EVENT).forEach((eventName) =>
      this._trtc!.on(eventName, (ev: any) => this.emit(eventName, ev)),
    )
  }

  /**
   * 推流状态变化回调
   * @param event
   */
  pusherStateChangeHandler(event: any) {
    this._trtc?.pusherEventHandler(event)
  }

  /**
   * 推流网络状态变化回调
   * @param event
   */
  pusherNetStatusHandler(event: any) {
    this._trtc?.pusherNetStatusHandler(event)
  }

  /**
   * 推流失败回调
   * @param event
   */
  pusherErrorHandler(event: any) {
    this._trtc?.pusherErrorHandler(event)
  }

  /**
   * 推流播放背景音回调
   * @param event
   */
  pusherBGMStartHandler(event: any) {
    this._trtc?.pusherBGMStartHandler(event)
  }

  /**
   * 推流背景音进度变化回调
   * @param event
   */
  pusherBGMProgressHandler(event: any) {
    this._trtc?.pusherBGMProgressHandler(event)
  }

  /**
   * 推流背景页播放完成回调
   * @param event
   */
  pusherBGMCompleteHandler(event: any) {
    this._trtc?.pusherBGMCompleteHandler(event)
  }

  /**
   * 推流音量变化提醒回调
   * @param event
   */
  pusherAudioVolumeNotify(event: any) {
    this._trtc?.pusherAudioVolumeNotify(event)
  }

  /**
   * 播放流状态变化回调
   * @param event
   */
  playerStateChange(event: any) {
    this._trtc?.playerEventHandler(event)
  }

  /**
   * 播放流全屏变化回调
   * @param event
   */
  playerFullscreenChange(event: any) {
    this._trtc?.playerFullscreenChange(event)
  }

  /**
   * 播放流网络变化回调
   * @param event
   */
  playerNetStatus(event: any) {
    this._trtc?.playerNetStatus(event)
  }

  /**
   * 播放流音量调整回调
   * @param event
   */
  playerAudioVolumeNotify(event: any) {
    this._trtc?.playerAudioVolumeNotify(event)
  }
}
