import CRC32 from 'crc32'
import HmacSHA256 from 'crypto-js/hmac-sha256'
import MD5 from 'crypto-js/md5'
import Base64 from 'crypto-js/enc-base64'
import Utf8 from 'crypto-js/enc-utf8'

interface ICalculateSignatureOptions {
  timestamp: number
  uri: string
  method: string
  data: any
  appKey: string
  appSecret: string
}

function calculateSignature(options: ICalculateSignatureOptions) {
  const { timestamp, uri, method, data, appKey, appSecret } = options
  const params: any = {
    method: method.toUpperCase(),
    url: uri,
    body_crc32: 0,
  }

  if (data) {
    const query = Object.keys(data)
      .sort()
      .map(
        (key) => `${key}=${encodeURIComponent(typeof data[key] === 'object' ? JSON.stringify(data[key]) : data[key])}`,
      )
      .join('&')
    

    params['body_crc32'] = Number('0x' + CRC32(query))
  }

  const stringToEncode = Object.keys(params)
    .sort()
    .map((key) => `${key}=${params[key]}`)
    .join('')
  const salt = MD5(`${appKey}${appSecret}${timestamp}`).toString().toUpperCase()

  const sign = HmacSHA256(stringToEncode, salt).toString().toUpperCase()

  return sign
}

interface IGenerateAuthorizationParams {
  timestamp: number
  appKey: string
  appSecret: string
  uri: string
  method: string
  data: any
}

export default function generateAuthorization(params: IGenerateAuthorizationParams) {
  const { timestamp, appKey, appSecret, uri, method, data } = params
  const sign = calculateSignature({
    timestamp,
    appKey,
    appSecret,
    uri,
    method,
    data,
  })
  const base64EncodeStr = `${appKey}:${sign}:${timestamp}`
  const auth = Base64.stringify(Utf8.parse(base64EncodeStr))
  return auth
}
