import { Subscribe } from './subscribe'
import { uuid } from './uuid'

type IEnhanceWechatMiniprogramWebSocketReadyState = {
  readonly NOTINITIALIZED: -1
  readonly OPEN: 1
  readonly CONNECTING: 0
  readonly CLOSING: 2
  readonly CLOSED: 3
}

export enum EnhanceWechatMiniprogramWebSocketReadyState {
  NOTINITIALIZED = -1,
  OPEN = 1,
  CONNECTING = 0,
  CLOSING = 2,
  CLOSED = 3,
}

const EnhanceWechatMiniprogramWebSocketReadyStateConstant: IEnhanceWechatMiniprogramWebSocketReadyState = {
  NOTINITIALIZED: -1,
  OPEN: 1,
  CONNECTING: 0,
  CLOSING: 2,
  CLOSED: 3,
}

interface IEnhanceWechatMiniprogramWebSocketWebsocket {
  id: string
}

type EnhanceWechatMiniprogramWebSocketTask = WechatMiniprogram.SocketTask & IEnhanceWechatMiniprogramWebSocketWebsocket

export type EnhanceWechatMiniprogramWebSocketEventMap = {
  open: (result: WechatMiniprogram.OnOpenListenerResult) => void
  close: (result: WechatMiniprogram.SocketTaskOnCloseListenerResult) => void
  message: (result: WechatMiniprogram.SocketTaskOnMessageListenerResult) => void
  error: (result: WechatMiniprogram.GeneralCallbackResult) => void
}

export type EnhanceWechatMiniprogramWebSocketOpenEvent = WechatMiniprogram.OnOpenListenerResult
export type EnhanceWechatMiniprogramWebSocketCloseEvent = WechatMiniprogram.SocketTaskOnCloseListenerResult
export type EnhanceWechatMiniprogramWebSocketMessageEvent = WechatMiniprogram.SocketTaskOnMessageListenerResult
export type EnhanceWechatMiniprogramWebSocketErrorEvent = WechatMiniprogram.GeneralCallbackResult

export class EnhanceWechatMiniprogramWebSocket extends Subscribe<EnhanceWechatMiniprogramWebSocketEventMap> {
  static readonly NOTINITIALIZED = -1
  static readonly CONNECTING = 0
  static readonly OPEN = 1
  static readonly CLOSING = 2
  static readonly CLOSED = 3

  private _readyState: EnhanceWechatMiniprogramWebSocketReadyState =
    EnhanceWechatMiniprogramWebSocketReadyState.NOTINITIALIZED

  get readyState() {
    return this._readyState
  }

  private _socketTask: EnhanceWechatMiniprogramWebSocketTask

  constructor(opts: WechatMiniprogram.ConnectSocketOption) {
    super()
    const socketTask = wx.connectSocket(opts)
    this._socketTask = Object.assign(
      {
        id: uuid(),
        // readyState: EnhanceWechatMiniprogramWebSocketReadyState.NOTINITIALIZED,
        ...EnhanceWechatMiniprogramWebSocketReadyStateConstant,
      },
      socketTask,
    )
    this._registarLifeEvent(this._socketTask)
    this._registarRawEvent(this._socketTask)
  }

  /**
   * 生命周期处理
   * @param socketTask
   */
  private _registarLifeEvent(socketTask: EnhanceWechatMiniprogramWebSocketTask) {
    socketTask.onOpen(() =>
      socketTask.id === this._socketTask.id
        ? (this._readyState = EnhanceWechatMiniprogramWebSocketReadyState.OPEN)
        : void 0,
    )
    socketTask.onClose(() =>
      socketTask.id === this._socketTask.id
        ? (this._readyState = EnhanceWechatMiniprogramWebSocketReadyState.CLOSED)
        : void 0,
    )
    socketTask.onError(() =>
      socketTask.id === this._socketTask.id
        ? (this._readyState = EnhanceWechatMiniprogramWebSocketReadyState.CLOSED)
        : void 0,
    )
  }

  /**
   * - 向微信小程序的SocketTask对象注册事件
   * - 因为微信小程序的SocketTask没有解绑事件的回调，所以重连导致问题
   * @param socketTask
   */
  private _registarRawEvent(socketTask: EnhanceWechatMiniprogramWebSocketTask) {
    const onOpen: WechatMiniprogram.OnOpenCallback = (result: WechatMiniprogram.OnOpenListenerResult) =>
      socketTask.id === this._socketTask.id ? this.emit('open', result) : void 0
    socketTask.onOpen(onOpen)

    const onClose: WechatMiniprogram.OnSocketCloseCallback = (
      result: WechatMiniprogram.SocketTaskOnCloseListenerResult,
    ) => (socketTask.id === this._socketTask.id ? this.emit('close', result) : void 0)
    socketTask.onClose(onClose)

    const onMessage: WechatMiniprogram.OnSocketMessageCallback = (
      result: WechatMiniprogram.SocketTaskOnMessageListenerResult,
    ) => (socketTask.id === this._socketTask.id ? this.emit('message', result) : void 0)
    socketTask.onMessage(onMessage)

    const onError: WechatMiniprogram.OnSocketErrorCallback = (result: WechatMiniprogram.GeneralCallbackResult) =>
      socketTask.id === this._socketTask.id ? this.emit('error', result) : void 0
    socketTask.onError(onError)
  }

  close(code: number, reason?: string): void {
    this._readyState = EnhanceWechatMiniprogramWebSocketReadyState.CLOSING
    this._socketTask.close({ code, reason })
  }

  send(data: string | ArrayBuffer) {
    this._socketTask.send({ data })
  }

  addEventListener<K extends keyof EnhanceWechatMiniprogramWebSocketEventMap>(
    name: K,
    callback: (
      ...args: Parameters<EnhanceWechatMiniprogramWebSocketEventMap[K]>
    ) => ReturnType<EnhanceWechatMiniprogramWebSocketEventMap[K]>,
    once?: boolean,
  ): void {
    if (once) {
      this.once(name, callback)
    } else {
      this.on(name, callback)
    }
  }

  removeEventListener<K extends keyof EnhanceWechatMiniprogramWebSocketEventMap>(
    name?: K,
    callback?: (
      ...args: Parameters<EnhanceWechatMiniprogramWebSocketEventMap[K]>
    ) => ReturnType<EnhanceWechatMiniprogramWebSocketEventMap[K]>,
  ): void {
    this.off(name, callback)
  }
}
