import { Controller } from './controller'
import { RealseeVRWebviewOptions } from './typings'

export default {
  properties: {
    props: String,
  },
  lifetimes: {
    created() {
      this.controller = new Controller(this)
    },
    attached() {
      this.controller.init(this.data.props)
    },
    detached() {
      this.controller.dispose()
    },
  },
  methods: {
    _handleWebViewError(err: Error) {
      this.controller.onWebViewError(err)
    },
    _handleWebViewLoad(ev) {
      console.info(`realsee vr-webview loaded url=(${ev.detail.src})`)
    },
  },
} as RealseeVRWebviewOptions
